.pre-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 60px;
	box-sizing: border-box;
	margin-bottom: 35px;

	.logo {
		font-size: 24px;
		line-height: 33px;
		font-weight: 700;
	}

	nav {
		ul {
			display: flex;
			align-items: center;
			list-style: none;
			gap: 20px;
			margin: 0;

			li {
				margin-bottom: 0;

				span {
					font-size: 18px;
					line-height: 25px;
					font-weight: 700;
					cursor: pointer;

					&.color-change {
						color: $error-clr;
					}
				}
			}
		}	
	}

	.user-details {
		position: relative;

		&-top {
			display: flex;
			gap: 7px;
			align-items: center;
		}

		&-btm {
			position: absolute;
			z-index: 2;
			background-color: $white-clr;
			border: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
			border-radius: 15px;
			padding: 40px;
			width: 260px;
			box-sizing: border-box;
			right: 0;
			top: 60px;

			ul {
				display: flex;
				flex-direction: column;
				align-items: flex-start;

				li {
					border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
					width: 100%;
					padding-bottom: 20px;

					&:last-child {
						padding-bottom: 0;
						border-bottom: 0;
					}
				}

				a {
					font-size: 18px;
					font-weight: 500;

					&.color-change {
						color: $error-clr;
					}
				}
			}
		}
		
		.user-img {
			width: 40px;
			height: 40px;
			background: $secondary-clr;
			border: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
			box-sizing: border-box;
			border-radius: 20px;
			display: flex;
			justify-content: center;
			align-items: center;
			overflow: hidden;
			font-weight: 600;
			font-size: 12px;
			text-transform: uppercase;
			text-decoration: none;
		}
	}
}

header {
	background-color: #676668;
    color: $white-clr;
    border-radius: 25px;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
	overflow: hidden;
	transform: translate3d(0, 0, 0);

	@media screen and (max-width: 980px) {
        height: auto;
		flex-direction: column;
    }

	@media screen and (max-width: 768px) {
		border-radius: 15px;
	}
	
}

.intro {
	background-color: $primary-clr;
	padding: 60px;
	width: 50%;
	box-sizing: border-box;
	height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: center;

	@media screen and (max-width: 980px) {
        height: auto;
		width: 100%;
		padding: 50px 30px;
		align-items: flex-start;
    }

	h1 {
		line-height: 1;
		margin-bottom: 15px;

		span {
			display: block;
			color: $secondary-clr;
		}
	}

	p {
		color: rgba($color: $white-clr, $alpha: 0.65);
		margin-bottom: $margin-bottom;
		max-width: 460px;

		@media screen and (max-width: 768px) {
			margin-bottom: 20px;
		}
	}
}

.video {
	width: 50%;
	box-sizing: border-box;
	background-color: #666;

	@media screen and (max-width: 980px) {
		width: 100%;
    }

	video {
		width: 100%;
	}
}


