html {
    box-sizing: border-box;
}

body {
    background: $white-clr;
    font-family: $font-family;
    font-weight: $weight-normal;
    line-height: $lineheight;
    color: $font-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    margin: 0;
    padding: 30px;

    @media screen and (max-width: 768px) {
      padding: 15px;
    }
}

.grey {
  background-color: #f1f1f1;
}

img {
    display: inline-block;
    vertical-align: middle;
    max-width: 100%;
    height: auto;
    -ms-interpolation-mode: bicubic;
}

.container {
    max-width: $global-width;
    width: 100%;
    margin: 0 auto;

    @media screen and (max-width: 1379px) {
        max-width: 100%;
    }
}

.primary-btn {
  background-color: $secondary-clr;
  color: $primary-clr;
  display: inline-block;
  padding: 20px 40px;
  border-radius: 55px;
}

.secondary-btn {
  background-color: $primary-clr;
  color: $white-clr;
  display: inline-block;
  padding: 20px 40px;
  border-radius: 55px;
  font-size: 17px;
  font-weight: 700;
}

.more-link {
  text-decoration: underline;
  font-size: 18px;
  font-weight: 500;
}

.page-wrap {
  display: flex;
  gap: 30px;
  align-items: flex-start;
}

.page-title {
  margin-bottom: 60px;
  margin-left: 60px;
}

main {
  flex: 1;
}

.content-wrap, .faqs-wrap .accordion-wrap {
  background-color: #fff;
  border-radius: 25px;
  padding: 60px;

  h2 {
    font-family: $font-family;
    font-weight: 500;
    line-height: 35px;
    font-size: 32px;
    margin-bottom: 30px;
    letter-spacing: -0.04rem;
  }

  .accordion-wrap {
    margin-bottom: 30px;
  }
}

.content-wrap {
  h2 {
    font-weight: 700;
  }
}

.mail-button {
  text-decoration: underline;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: $white-clr;
  }
  ::-moz-placeholder { /* Firefox 19+ */
    color: $white-clr;
  }
  :-ms-input-placeholder { /* IE 10+ */
    color: $white-clr;
  }
  :-moz-placeholder { /* Firefox 18- */
    color: $white-clr;
  }
