.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 10;
	background: rgba(0, 0, 0, 0.75);
}

.modal {
	position: fixed;
	top: 10vh;
	left: calc(50% - 325px);
	width: 650px;
	z-index: 100;
	overflow: hidden;
	background-color: #fff;
	padding: 60px 80px;
	box-sizing: border-box;
	border-radius: 25px;
	text-align: center;

	h2 {
		font-size: 60px;
		line-height: 1;
		letter-spacing: -0.04rem;
		font-weight: 500;
		margin-bottom: 30px;
	}

	p {
		font-size: 18px;
		line-height: 25px;
		margin-bottom: 20px;
		color: rgba($color: $primary-clr, $alpha: 0.65);
	}

	&-link {
		font-size: 18px;
		line-height: 25px;
		margin-bottom: 15px;

		span {
			text-decoration: underline;
			cursor: pointer;
			color: rgba($color: $primary-clr, $alpha: 0.65);
		}
	}

	.form-row {
		margin-bottom: 20px;
		position: relative;

		input {
			text-align: left;
			padding: 0px 40px;
			width: 100%;
			box-sizing: border-box;
		}

		textarea {
			text-align: left;
			width: 100%;
		}

		.btn {
			margin-top: 15px;
		}

		.icon-password {
			position: absolute;
			top: 20px;
			right: 20px;
		}
	}

}

@media (min-width: 768px) {
	.modal {
		left: calc(50% - 20rem);
		width: 40rem;
	}
}

.config-details {
	margin: 0;

	.row {
		list-style: none;
		display: flex;
		justify-content: space-between;
		border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
		padding-bottom: 20px;
		margin-bottom: 20px;
		font-size: 18px;
		font-weight: 500;

		&:last-child {
			border-bottom: 0;
			margin-bottom: 40px;
		}

		span {
			&:first-child {
				opacity: 35%;
			}
		}
	}
}
