.setup-step {
	background-color: $white-clr;
	border-radius: 25px;
	padding: 40px 60px;
	margin-bottom: 30px;
	display: flex;
	gap: 30px;
	position: relative;

	&:last-child {
		margin-bottom: 0;
	}

	.setup-num {
		min-width: 60px;
		width: 60px;
		height: 60px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: $secondary-clr;
		border-radius: 50%;
		font-weight: 700;
		font-size: 24px;
		position: relative;
		z-index: 1;
	}

	h2 {
		font-size: 24px;
		margin-top: 15px;
		margin-bottom: 15px;
	}

	p {
		font-size: 18px;
	}

	.instructions {
		margin: 20px 0;
		max-width: 590px;

		&.big {
			max-width: initial;
		}
	}

	.payment-img {
		margin-top: 60px;
	}

	.form-row {
		input {
			border-radius: 10px;
			text-align: left;
			padding: 20px;
			width: 100%;
			box-sizing: border-box;
		}

		&.calendar {
			position: relative;

			input {
				padding-right: 55px;
				-webkit-appearance: none;
				-moz-appearance: none;
				appearance: none;
			}

			.icon-calendar {
				position: absolute;
				top: 20px;
				right: 20px;
			}
		}
	}

	select {
		border-radius: 10px;
		text-align: left;
		padding: 20px;
		width: 100%;
		box-sizing: border-box;
		background-repeat: no-repeat;
		background-position: top 30px right 30px;
		-webkit-appearance: none;
   		-moz-appearance: none;
        appearance: none;
	}
}

.setup-content {
	color: rgba($color: $primary-clr, $alpha: 0.65);
	font-size: 18px;

	.reopen {
		font-weight: 500;
		text-decoration: underline;
		cursor: pointer;
		color: $primary-clr;
	}
}

.setup-accordion {
	padding: 60px;

	h2 {
		font-size: 32px;
		margin-bottom: 40px;
	}
}

.setup-wrap {
	margin-bottom: 60px;
}

.setup-wrap, .setup-step {
	position: relative;

	&::before {
		content: '';
		width: 5px;
		height: 100%;
		background-color: $secondary-clr;
		top: 0;
		left: 88px;
		position: absolute;
	}
}

.first-step::before {
	top: 60px;
}

.last-step::before {
	height: 60px;
}

.machines-list {
	display: flex;
	gap: 20px;
	margin-bottom: 20px;
	margin-top: 35px;

	h3 {
		font-size: 24px;
		margin-bottom: 15px;
	}
}

.machine-block {
	border: 2px solid rgba(0, 0, 0, .15);
	border-radius: 10px;
	padding: 30px;
	box-sizing: border-box;

	&:hover {
		background-color: rgba($color: $primary-clr, $alpha: 0.15);
	}

	&.active {
		background-color: $primary-clr;
		color: $white-clr;
		border: 2px solid $secondary-clr;

		svg path {
			fill: $white-clr;
			color: $white-clr;
		}
	}

	ul {
		list-style: none;
		margin: 0;

		li {
			display: flex;
			gap: 10px;

			span:first-child {
				width: 90px;
				text-align: right;
				opacity: 35%;
			}
		}
	}
}

.packages-list {
	display: flex;
	list-style: none;
	margin-top: 35px;
	margin-bottom: 0;
	margin-left: 0;
	gap: 10px;

	li {
		border: 2px solid rgba(0, 0, 0, .15);
		border-radius: 10px;
		padding: 30px;
		box-sizing: border-box;
		position: relative;

		div {
			margin-bottom: 15px;
		}

		span {
			display: block;
			font-size: 24px;
			font-weight: 700;
			line-height: 1;

			&.popular-tag {
				position: absolute;
				font-size: 10px;
				font-weight: 500;
				line-height: 1;
				padding: 4px 8px;
				background-color: $secondary-clr;
				border-radius: 40px;
				top: -11px;
			}
		}

		.hours {
			font-size: 40px;
		}

		.price {
			color: #039700;
		}

		&:hover {
			background-color: rgba($color: $primary-clr, $alpha: 0.15);
		}

		&.active {
			background-color: $primary-clr;
			color: $white-clr;
			border: 2px solid $secondary-clr;

			.price {
				color: $white-clr;
			}
		}
	}
}

[data-disabled="true"] {
	opacity: 50%;

	&:hover {
		background-color: $white-clr;
	}
}

.siderbar {
	p {
		 font-size: 18px;
		 margin: 30px 0;
	}

	.stripe_logo {
		margin-top: 30px;

		img {
			width: 150px;
		}
	}
}
