.machines {
	margin-bottom: 60px;

	p {
		font-size: 18px;
	}
}

.machine {
	display: flex;
	justify-content: space-between;
	padding-bottom: 60px;
	margin-bottom: 60px;
	padding-top: 30px;
	border-bottom: 2px solid rgba($color: #000, $alpha: 0.15);

	&-icon {
		width: 48px;
		height: 48px;
		border: 2px solid rgba($color: #000, $alpha: 0.15);
		border-radius: 5px;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 8px;
		box-sizing: border-box;
		margin-right: 15px;
	}

	&-info {
		display: flex;
		align-items: center;
	}

	&-name {
		font-weight: 700;
		margin-bottom: 5px;

		span {
			font-size: 14px;
			opacity: 50%;

			&.dot {
				font-weight: 800;
				font-size: 30px;
				position: relative;
				top: -3px;
				opacity: 1;
				width: 4px;
				height: 4px;
				display: inline-block;
				background-color: $primary-clr;
				border-radius: 50%;
				margin: 0 5px;
			}
		}
	}

	&-status {
		font-size: 14px !important;
		display: flex;
		align-items: center;

		.offline {
			width: 16px;
			height: 16px;
			background: $error-clr;
			border: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
			border-radius: 50%;
			display: inline-block;
			margin-right: 5px;
		}

		.processing {
			width: 16px;
			height: 16px;
			background: #FFCE00;
			border: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
			border-radius: 50%;
			display: inline-block;
			margin-right: 5px;
		}

		.online {
			width: 16px;
			height: 16px;
			background: $secondary-clr;
			border: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
			border-radius: 50%;
			display: inline-block;
			margin-right: 5px;
		}

		.dot {
			font-weight: 500;
			font-size: 30px;
			position: relative;
			top: 0px;
			width: 2px;
			height: 2px;
			display: inline-block;
			background-color: rgba($color: $primary-clr, $alpha: 0.65);
			border-radius: 50%;
			margin: 0 5px;
		}

		.config-link {
			text-decoration: underline;
			cursor: pointer;
		}
	}

	&-btn {
		.btn {
			&:disabled,
			&[disabled]{
				background-color: rgba($color: $primary-clr, $alpha: 0.15);
				color: rgba($color: $primary-clr, $alpha: 0.35);

				&:hover {
					text-decoration: none;
				}
			}
		}

		.btn-wrap {
			display: flex;
			gap: 15px;
		}
	}
}
