.siderbar {
    background-color: rgba($color: $primary-clr, $alpha: 0.08);
    padding: 60px;
    border-radius: 25px;
    width: 23%;

    h2 {
        font-family: $font-family;
        font-weight: 700;
        line-height: 35px;
        font-size: 32px;
        margin-bottom: 30px;
        letter-spacing: -0.04rem;
    }

    h3 {
        font-family: $font-family;
        font-weight: 500;
        line-height: 30px;
        font-size: 24px;
        margin-bottom: 30px;
        letter-spacing: -0.02rem;
    }

    ul {
        list-style: none;
        margin-left: 0;

        li {
            padding: 15px 0;
            border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);

            &:last-child {
                border-bottom: 0;
            }

            a {
                font-weight: 500;
                line-height: 30px;
                font-size: 24px;
                letter-spacing: -0.02rem;
                text-decoration: underline;
            }
        }
    }

    .btn {
        width: 100%;
    }

    .selected-values {
        border-top: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
        padding-top: 10px;
        margin: 15px 0;

        li {
            padding: 0;
            display: flex;
            justify-content: space-between;
            border-bottom: 0;

            span:first-child {
                opacity: 45%;
            }

            .large-txt {
                font-size: 40px;
                font-weight: 700;
                line-height: 40px;
            }
        }

        &.first {
            border: none;
            padding: 0;
        }
    }
}

.config-sidebar{
	margin: 0;

	.row {
		list-style: none;
		display: flex;
		justify-content: space-between;
		border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
		padding-bottom: 10px;
		margin-bottom: 0;
		font-size: 18px;
		font-weight: 500;

		&:last-child {
			border-bottom: 0;
			margin-bottom: 40px;
		}

		span {
			&:first-child {
				opacity: 35%;
			}
		}
	}
}
