/* Acordeon styles */
.accordion {
	&-wrap {
		margin-bottom: 40px;
	}

	position: relative;
	margin-bottom: 1px;
	width: 100%;
	overflow: hidden;

	input {
		position: absolute;
		opacity: 0;
		z-index: -1;
	}

	label {
		position: relative;
		padding: 15px 0;
		line-height: 24px;
		cursor: pointer;
		
		font-family: $font-family;
		font-weight: 500;
		color: $primary-clr;
		margin-bottom: 2px;
		display: block;
		font-size: 18px;
		padding-right: 25px;

		&::after {
			position: absolute;
			right: 0;
			top: 12px;
			display: block;
			text-align: center;
			-webkit-transition: all .35s;
			-o-transition: all .35s;
			transition: all .35s;
			font-size: 20px;
			font-weight: 700;
		}
	}

	&-content {
		max-height: 0;
		overflow: hidden;
		-webkit-transition: max-height .35s;
		-o-transition: max-height .35s;
		transition: max-height .35s;
		border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);

		p {
			font-weight: 400;
			font-size: 18px;
			line-height: 1.5em;
			color: rgba($color: $primary-clr, $alpha: 0.65);
			margin-bottom: 1em;
			margin-right: 25px;
		  }
	}

	/* :checked */
	input:checked ~ .accordion-content {
		max-height: 10em;
	  }
	/* Icon */
	input[type=radio] + label::after {
		content: "+";
		float: right;
	}
	input[type=radio]:checked + label::after {
	/*   transform: rotate(315deg); */
		content: "\2212";
	}

	input[type=radio]:checked + label {
		padding-bottom: 0;
	}
}
  
  
  
  