.payment-status {
    padding-bottom: 60px;
    margin-bottom: 60px;
    border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);

    h1 {
        font-size: 50px;
        color: $success-clr;
        margin-bottom: 20px;
    }

    p {
        margin-bottom: 40px;
        font-size: 24px;
    }
}

.payment-failed {
    border-bottom: none;
    margin-bottom: 0;

    h1 {
        color: #DE2800;
    }
}

.payment-summary {
    h2 {
        font-size: 24px;
        margin-bottom: 30px;
        letter-spacing: -0.02rem;
    }

    p {
        font-size: 18px;
        font-weight: 500;
        color: rgba($color: $primary-clr, $alpha: 0.35);

        a {
            text-decoration: underline;
            color: $primary-clr;
        }
    }

    ul {
        margin-left: 0;
        margin-bottom: 30px;

        li {
            list-style: none;
            padding-bottom: 15px;
            margin-bottom: 15px;
            border-bottom: 2px solid rgba($color: $primary-clr, $alpha: 0.15);
            display: flex;
            justify-content: space-between;

            span {
                &:first-child {
                    opacity: 35%;
                }
            }
        }
    }
}
