.empty-dashboard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 60px;
    border: 2px dashed rgba($color: $primary-clr, $alpha: 0.15);
    border-radius: 15px;
    min-height: 285px;

    p {
        font-size: 24px;
        line-height: 30px;
        font-weight: 500;
        margin-bottom: 20px;
    }
}