.btns-wrap {
	display: flex;
	gap: 15px;
}
.btn {
	padding: 20px 40px;
	// width: 220px;
	height: 65px;
	line-height: 1.25;
	border-radius: 55px;
	box-sizing: border-box;
	text-align: center;
	font-size: 18px;
	font-weight: 700;
	border: none;
	font-family: $font-family;
	cursor: pointer;

	@media screen and (max-width: 768px) {
		padding: 15px 22px;
		height: 50px;
		width: auto;
		font-size: 14px;
	}

	&:hover {
		text-decoration: underline;
	}

	&-sm {
		height: 45px;
		padding: 10px 20px;
	}

	&-dark {
		background-color: $primary-clr;
		color: $white-clr;

		&:hover {
			background-color: $btn-dark-hover;
		}

		&.border {
			border: 2px solid $secondary-clr;
			color: $secondary-clr;

			&:hover {
				border: 2px solid $secondary-clr;
			}
		}
	}

	&-light {
		background-color: $secondary-clr;
		color: $primary-clr;

		&:hover {
			background-color: $btn-light-hover;
		}

		&.border {
			border: 2px solid $primary-clr;

			&:hover {
				border: 2px solid $primary-clr;
			}
		}

		&.off {
			border: 2px solid $error-clr;
			background-color: $white-clr;
			color: $error-clr;

			&:hover {
				border: 2px solid $error-clr;
				background-color: $white-clr;
				color: $error-clr;
			}
		}
	}
}
