.welcome {
	width: calc(100vw - 60px);
	height: calc(100vh - 60px);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: $secondary-clr;
	box-sizing: border-box;
	border-radius: 25px;
	font-size: 50px;
	font-weight: 700;
	line-height: 55px;
	text-align: center;

	span {
		display: block;
	}
}
